	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Apple Shop-in-Shop in Hamburg","date":"21 Dec 2004","slug":"/2004-12-21-apple-shopinshop-in-hamburg/","tags":"Y2004"},"html":"<p>We don't yet have any Apple Stores in Germany, but we do now have an <a title=\"heise online - Saturn eröffnet seinen ersten Apple Shop-in-Shop\" href=\"http://www.heise.de/newsticker/meldung/54478\">Apple Shop-in-Shop</a> at the Saturn Megastore in downtown Hamburg, in case you're looking to pick up some white plastic stuff for the holidays. (Actually, Saturn has always sold Macs in the back corner of the basement... it looks like they just found a new corner and with new wallpaper and Apple logos.)</p>"},"next":{"fields":{"title":"Happy Holidays","date":"19 Dec 2004","slug":"/2004-12-19-happy-holidays/"}},"previous":{"fields":{"title":"Bloglines: Volume!","date":"21 Dec 2004","slug":"/2004-12-21-bloglines-volume/"}}},{"node":{"fields":{"title":"Happy Holidays","date":"19 Dec 2004","slug":"/2004-12-19-happy-holidays/","tags":"Y2004"},"html":"<p>My parents arrived safely on Friday, the <a href=\"http://www.linde-gastherapeutics.com/International/Web/LG/templ/Likelgalhtempl.nsf/DocByAlias/hc_trav\">oxygen concentrator</a> for my father is up and running, Mama and I are both off work, Christopher has his grandparents wrapped around his little finger, we're getting the Christmas tree today, the Christmas baking and cooking are planned for this week, and of course the big birthday on the 27th, so don't expect much writing here for the next two weeks. Maybe some pictures. We'll see.</p>\n<p><img alt=\"fam_xmas2004.jpg\" src=\"http://www.papascott.de/archives/fotos/fam_xmas2004.jpg\" width=\"320\" height=\"240\" /></p>"},"next":{"fields":{"title":"Just Cos I Eat Worms","date":"17 Dec 2004","slug":"/2004-12-17-just-cos-i-eat-worms/"}},"previous":{"fields":{"title":"Apple Shop-in-Shop in Hamburg","date":"21 Dec 2004","slug":"/2004-12-21-apple-shopinshop-in-hamburg/"}}},{"node":{"fields":{"title":"Just Cos I Eat Worms","date":"17 Dec 2004","slug":"/2004-12-17-just-cos-i-eat-worms/","tags":"Y2004"},"html":"<p>Funny, with all the <a title=\"Movable Type Publishing Platform: Comment spam load issue\" href=\"http://www.movabletype.org/news/2004/12/comment_spam_load_issue.shtml\">hoopla about comment spam lately</a>, I haven't gotten a single spam for over a week. My MT-Blacklist log is empty. I had to write a spam myself to convince myself it was still working.</p>\n<p>My anti-spam strategy of boring content must be working. Especially now that my Google Page Rank has dropped to 6, I'm simply no longer worth the bother, I hope.</p>\n<p>Actually, I think <a title=\"MT Plugin: Conversation Killer\" href=\"http://www.chompy.net/lab/mt/conversationkiller/\">closing comments on old entries</a> helps more than anything.</p>"},"next":{"fields":{"title":"Spams for Money","date":"15 Dec 2004","slug":"/2004-12-15-spams-for-money/"}},"previous":{"fields":{"title":"Happy Holidays","date":"19 Dec 2004","slug":"/2004-12-19-happy-holidays/"}}}],"pathPrefix":"","first":false,"last":false,"index":363,"pageCount":1101}}

